<template>
	<div id="board">
		<div v-show="win==0">
			<p v-show="player==1"><strong>Player: </strong><i class="fas fa-times" style="margin-left: 0.25rem"></i></p>
			<p v-show="player==2"><strong>Player: </strong><i class="far fa-circle" style="margin-left: 0.25rem"></i></p>
		</div>
		<div class="flex">
			<p v-show="win==1"><i class="fas fa-times"></i> <strong>Win</strong></p>
			<p v-show="win==2"><i class="far fa-circle"></i> <strong>Win</strong></p>
			<div v-show="win!=0" class="button" @click="restart()"><strong style="color: #ebebeb">Restart</strong></div>
		</div>
		<div v-for="r in rows" :key="r" class="row" :class="{cannotClick:avoidClick}">
			<div v-for="c in cols" :key="c" class="col" @click="change(r,c)" :class="{checkLast:checkLast(r,c)}">
				<p v-show="check(r,c)==1" class="square"><i class="fas fa-times"></i></p>
				<p v-show="check(r,c)==2" class="square"><i class="far fa-circle"></i></p>
			</div>
		</div>
	</div>
</template>

<script>
export default {
	name: 'Board',
	components: {},
	data(){
		return{
			rows: 15,
			cols: 15,
			player: 1,
			x: [],
			o: [],
			checkLastR: -1,
			checkLastC: -1,
			win: 0,
			avoidClick: 0,
		}
	},
	methods: {
		change: function(r,c){
			if(this.player==1 && this.o.indexOf('r'+r+'c'+c)==-1 && this.x.indexOf('r'+r+'c'+c)==-1){
				this.x.push('r'+r+'c'+c);
				this.checkWin(r,c,this.player);
				this.player=2;
				this.checkLastR = r;
				this.checkLastC = c;
			}
			else
				if(this.player==2 && this.x.indexOf('r'+r+'c'+c)==-1 && this.o.indexOf('r'+r+'c'+c)==-1){
					this.o.push('r'+r+'c'+c);
					this.checkWin(r,c,this.player);
					this.player=1;
					this.checkLastR = r;
					this.checkLastC = c;
				}
		},
		check: function(r,c){
			if(this.x.indexOf('r'+r+'c'+c)!=-1)
				return 1;
			else
				if(this.o.indexOf('r'+r+'c'+c)!=-1)
					return 2;
		},
		checkLast: function(r,c){
			if(this.checkLastR==r && this.checkLastC == c)
				return 1;
		},
		checkWin: function(r,c,player){
			var countR = 1;
			var countC = 1;
			var countLR = 1;
			var countRL = 1;
			var i=1; 
			if(player==1){
				// check win hang ngang
				for(i=1;i<5;i++){
					if(this.x.indexOf('r'+r+'c'+(c+i))!=-1)
						countR++;
					if(this.x.indexOf('r'+r+'c'+(c-i))!=-1)
						countR++;
				}
				// check win hang roc
				for(i=1;i<5;i++){
					if(this.x.indexOf('r'+(r+i)+'c'+c)!=-1)
						countC++;
					if(this.x.indexOf('r'+(r-i)+'c'+c)!=-1)
						countC++;
				}
				// check win hang cheo tu trai sang phai
				for(i=1;i<5;i++){
					if(this.x.indexOf('r'+(r+i)+'c'+(c+i))!=-1)
						countLR++;
					if(this.x.indexOf('r'+(r-i)+'c'+(c-i))!=-1)
						countLR++;
				}
				// check win hang cheo tu phai sang trai
				for(i=1;i<5;i++){
					if(this.x.indexOf('r'+(r+i)+'c'+(c-i))!=-1)
						countRL++;
					if(this.x.indexOf('r'+(r-i)+'c'+(c+i))!=-1)
						countRL++;
				}
				// return winner
				if(countR==5||countC==5||countLR==5||countRL==5){
					this.win=1;
					this.avoidClick=1;
					return 1;
				}
			}
			if(player==2){
				// check win hang ngang
				for(i=1;i<5;i++){
					if(this.o.indexOf('r'+r+'c'+(c+i))!=-1)
						countR++;
					if(this.o.indexOf('r'+r+'c'+(c-i))!=-1)
						countR++;
				}
				// check win hang roc
				for(i=1;i<5;i++){
					if(this.o.indexOf('r'+(r+i)+'c'+c)!=-1)
						countC++;
					if(this.o.indexOf('r'+(r-i)+'c'+c)!=-1)
						countC++;
				}
				// check win hang cheo tu trai sang phai
				for(i=1;i<5;i++){
					if(this.o.indexOf('r'+(r+i)+'c'+(c+i))!=-1)
						countLR++;
					if(this.o.indexOf('r'+(r-i)+'c'+(c-i))!=-1)
						countLR++;
				}
				// check win hang cheo tu phai sang trai
				for(i=1;i<5;i++){
					if(this.o.indexOf('r'+(r+i)+'c'+(c-i))!=-1)
						countRL++;
					if(this.o.indexOf('r'+(r-i)+'c'+(c+i))!=-1)
						countRL++;
				}
				// return winner
				if(countR==5||countC==5||countLR==5||countRL==5){
					this.win=2;
					this.avoidClick=1;
					return 1;
				}
			}
		},
		restart: function(){
			this.player = 1;
			this.x = [];
			this.o = [];
			this.win = 0;
			this.avoidClick = 0;
			this.checkLastR = -1;
			this.checkLastC = -1;
		}
	},
	pops: {
		
	}
}
</script>


<style scoped>
	.cannotClick{
		pointer-events: none;
	}

	.flex{
		display: flex;
	}

	.row{
		display: flex;
		border-left: 1px solid;
		border-right: 1px solid;
	}

	.col{
		cursor: pointer;
		box-sizing: border-box;
		height: 2rem;
		width: 2rem;
		border: 1px solid #111;
	}
	
	.square{
		position: relative;
	}

	.square i{
		position: absolute;
		top:50%;
		left: 50%;
		transform: translate(-50%,-50%);
	}

	i.fa-times{
		margin-right: 0.5rem;
		color: royalblue;
	}

	i.fa-circle{
		margin-right: 0.5rem;
		color: red;
	}

	.checkLast{
		background-color: #999999c9;
	}

	.button{
		height: 1rem;
		padding: 0.5rem 1.5rem;
		margin-top: 0.5rem;
		border-radius: 5px;
		cursor: pointer;
		margin-left: 2rem;
		background-color: royalblue;
	}
	@media only screen and (min-width: 420px) {
		#board{
			position: absolute;
			left: 50%;
			transform: translate(-50%);
		}
	}
</style>